import React, { useContext } from "react";
import "./Portfolio.css";
import { Swiper, SwiperSlide } from "swiper/react"
import "swiper/css";
import Sidebar from "../../img/sidebar.png";
import Ecommerce from "../../img/ecommerce.png";
import HOC from "../../img/hoc.png";
import MusicApp from "../../img/musicapp.png";
import { themeContext } from "../../Context";
const Portfolio = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div className="portfolio" id="portfolio" style={{margin:"0px",padding:"0px"}}>
      {/* heading */}
      <span style={{color: darkMode?'white': ''}}>Recent Projects</span>
      <span>Portfolio</span>

      {/* slider */}
      <Swiper
        spaceBetween={30}
        slidesPerView={3}
        grabCursor={true}
        className="portfolio-slider"
      >
        <SwiperSlide>
        <div style={{fontWeight:"bold",margin :"5px"}}>banggood.com</div>
       <a href="https://warm-lollipop-0c0908.netlify.app/"><img src={Sidebar} alt="" /></a>
          
        </SwiperSlide>
        <SwiperSlide>
          <div style={{fontWeight:"bold",margin :"5px"}}>Fraazo.com</div>
          <a href="https://resilient-narwhal-7f4120.netlify.app/"> <img style={{width:"200px"}} src={Ecommerce} alt="" /></a>
         
        </SwiperSlide>
        <SwiperSlide>
        <div style={{fontWeight:"bold",margin :"5px"}}>buffer.com</div>
        <a href="https://cerulean-gingersnap-efb06b.netlify.app/"> <img src={MusicApp} alt="" /></a>
         
        </SwiperSlide>
        {/* <SwiperSlide>
          <img src={HOC} alt="" />
        </SwiperSlide> */}
      </Swiper>
    </div>
  );
};

export default Portfolio;
